



































































@import "src/stylesheets/variables";

#app {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }

.section {
  flex: 1; }

.hero.is-info {
  background: $main-color; }

.navbar-item {
  color: $navbar-item-color; }

a.navbar-item {
  &:hover {
    background-color: transparent; } }

.scrollToTop {
  position: fixed;
  bottom: 10px;
  right: 10px;
  color: $main-color;
  &:hover {
    cursor: pointer; } }

.footer a:not(.icon), .footer a:visited:not(.icon) {
  border-bottom: none; }

@media screen and (max-width: 1023px) {
  .navbar-menu {
    background-color: initial;
    box-shadow: initial;
    padding: initial; } }
